import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import VModal from 'vue-js-modal'
 
Vue.use(VModal)
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
